@import './normalize.scss';
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Montserrat:700,900');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,700;0,800;1,300;1,400;1,700;1,800&display=swap');
@import './fonts.scss';
@import './variables.scss';
@import './mixins.scss';

html {
  scroll-behavior: smooth;
}
body {
  font-family: 'Open Sans', 'Roboto Condensed', 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  height: 100vh;
  position: relative;
  overflow-x: hidden;
  user-select: none;

  color: #000;
  transition: 0.2s;
}
::-webkit-scrollbar {
  width: 0;
}
header {
  z-index: 10;
}
section {
  z-index: 5;
}

.hidden {
  display: none;
}

.wrapper {
  max-width: 1020px;
  margin: 0 auto;

  @media (max-width: $mediaLaptops) {
    max-width: 965px;
  }
  @media (max-width: $mediaSmallDesktops) {
    max-width: 650px;
  }
  @media (max-width: $mediaTablets) {
    max-width: 625px;
  }
  @media (max-width: $mediaSmallTablets) {
    max-width: 400px;
  }
  @media (max-width: $mediaPhones) {
    max-width: 350px;
  }
  @media (max-width: $mediaSmallPhones) {
    max-width: 260px;
  }
}

.columns {
  position: relative;
  padding: 0 20px;

  @media (max-width: $mediaPhones) {
    padding: 0 5px;
  }
}

.pulse {
  animation: pulse 1.5s ease-in-out 0.5s infinite;
}
@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}
