.Page404 {
  position: relative;
  height: 100vh;
  background: #030005;
}

.Page404-not-found {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.Page404-not-found {
  max-width: 767px;
  width: 100%;
  line-height: 1.4;
  text-align: center;

  &__link {
    font-family: 'Montserrat', sans-serif;
    display: inline-block;
    text-transform: uppercase;
    color: #ff005a;
    text-decoration: none;
    border: 2px solid;
    background: transparent;
    padding: 10px 40px;
    font-size: 14px;
    font-weight: 700;
    -webkit-transition: 0.2s all;
    transition: 0.2s all;

    &:hover {
      color: #8400ff;
    }
  }
}

.Page404-not-found-404 {
  position: relative;
  height: 180px;
  margin-bottom: 20px;
  z-index: -1;

  &__title {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    font-size: 224px;
    font-weight: 900;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: -12px;
    color: #030005;
    text-transform: uppercase;
    text-shadow: -1px -1px 0px #8400ff, 1px 1px 0px #ff005a;
    letter-spacing: -20px;
  }

  &__subtitle {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    left: 0;
    right: 0;
    top: 110px;
    font-size: 42px;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    text-shadow: 0px 2px 0px #8400ff;
    letter-spacing: 13px;
    margin: 0;
  }
}

@media only screen and (max-width: 767px) {
  .Page404-not-found-404__subtitle {
    font-size: 24px;
  }
}

@media only screen and (max-width: 480px) {
  .Page404-not-found-404__title {
    font-size: 162px;
  }
  .Page404-not-found-404__subtitle {
    font-size: 15px;
  }
}
